import React from 'react'
import { Link } from 'react-router-dom'

function SectionBtn({title, author, id}) {
    return(
        <section>
            <Link to={'/' + id}>
                <h2>{title}</h2>
                <h3>Wątek stworzony przez użytkownika {author}</h3>
                <hr/>
            </Link>
        </section>
    )
}

export default SectionBtn