import React, { useState, useEffect } from 'react'
import { useParams, Link } from 'react-router-dom'
import request from './request'
import './PostPage.scss'

function PostPage() {
    const [content, setContent] = useState(<h1>Ładowanie</h1>)
    const [answerAuthor, setAnswerAuthor] = useState('')
    const [answerText, setAnswerText] = useState('')
    const { id } = useParams()
    const addAnswer = () => {
        const data = {
            author: answerAuthor,
            text: answerText
        }
        request('http://localhost:4000/posts/' + id, 'POST', data).then(() => window.location.reload(false))
        
    }

    useEffect(() => {
        request('http://localhost:4000/posts/' + id, 'GET')
            .then(({title, author, text, answers}) => {
                const answersSections = answers.map(({author, text}, index) => {
                    return(
                        <section key={index} >
                           <h2> {author} </h2>
                           <p> {text} </p>
                        </section>
                    )
                }, [])
                setContent(
                    <div className='PostPage'>  
                        <Link to='/' id='back'>
                            Powrót
                        </Link>
                        <h1>{title}</h1>
                        <section>                            
                            <h2>{author}</h2>
                            <p>{text}</p>
                        </section>
                        {answersSections}
                        <section>
                            <h2>Twoje imie</h2>
                            <input
                                type='text'
                                value={answerAuthor}
                                onChange={e => setAnswerAuthor(e.target.value)}
                            />
                            <h2>Tekst</h2>
                            <textarea
                                value={answerText}
                                onChange={e => setAnswerText(e.target.value)}
                            />                        
                            <div
                                className='addAnswer'
                                onClick={() => addAnswer()}
                            >
                                Dodaj odpowiedź
                            </div>
                            <div style={{clear: 'both'}} />
                        </section>
                    </div>
                )
            })
    })
    return content
}

export default PostPage