import React, { useEffect, useState } from 'react'
import request from './request'
import SectionBtn from './SectionBtn'
import { Link } from 'react-router-dom'
import './Sections.scss'

function Sections() {
  const [sections, setSections] = useState([])
  useEffect(() => {
    request('http://localhost:4000/posts', 'GET')
        .then(({posts}) => {
          const newBtns = posts.map(({author, title, id}) => <SectionBtn author={author} title={title} id={id} key={id} />)
          setSections(newBtns)
        })
    })
    
  return (
    <div className='Sections'>
      <Link to='/newPost' id='createPost'>
        Utwórz post
      </Link>
      { sections }
    </div>
  )
}

export default Sections
